.tournament-participants__list-wrapper {
	border-top: 1px solid var(--palette-live-grey-7);
	margin: 20px 16px 0 16px;

	.tournament-participants__title {
		display: flex;
		align-items: center;

		.tournament-participants__count {
			display: flex;
			align-items: center;

			.tournament-participants__separator {
				display: inline-block;
				width: 2.5px;
				height: 18px;
				background-color: var(--palette-live-grey-7);
				margin: 0 8px;
			}
		
			.tournament-participants__count-max {
				color: var(--palette-live-grey-7);
				position: relative;
			}
		}
	}

	.tournament-participants-list {
		.tournament-participants__item {
			align-items: center;
			border-bottom: 1px solid var(--palette-live-grey-11);
			display: flex;
			padding: 0 0 0 16px;
			cursor: pointer;

			.tournament-participants__number {
				align-items: center;
				border-radius: 100%;
				display: flex;
				font-size: var(--font-size-18);
				color: var(--palette-live-grey-0);
				font-weight: var(--font-weight-regular);
				justify-content: center;
				margin: 0 16px 0 0;
			}

			.tournament-participants__name {
				font-size: var(--font-size-18);
				font-weight: var(--font-weight-regular);
				color: var(--palette-live-grey-0);
				margin: 0 0 0 0;
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				min-height: 48px;
				box-sizing: border-box;

				&.display-name {
					height: 65.5px;
				}

				p {
					margin: 0;
				}

				.tournament-participants__display-name {
					margin-bottom: 2px;
					font-size: var(--font-size-16);
					color: var(--palette-live-grey-7);
				}
			}

			.tournament_participants__block {
				height: 48px;
				width: 80px;
				border-left: 1px solid var(--palette-live-grey-5);
				box-sizing: border-box;
				fill: var(--palette-live-grey-6);
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-left: 10px;

				.tournament_participants__strength {
					width: 44px;
					display: flex;
					align-items: center;
					justify-content: center;
					margin: 0;
					font-size: var(--font-size-18);
					height: 100%;
					border-right: 1px solid var(--palette-live-grey-5);
				}

				.tournament_participants__dnd {
					width: 40px;
					display: flex;
					align-items: center;
					justify-content: center;

					svg {
						width: 20px;
					}
				}
			}

			.tournament-participants__names {
				flex: 1;
				padding: 11.5px 0;
				box-sizing: border-box;

				.tournament-participants__palyer--first {
					font-size: var(--font-size-18);
					font-weight: var(--font-weight-regular);
					color: var(--palette-live-grey-0);
					margin: 0 0 2px 0;
				}

				.tournament-participants__palyer--second {
					font-size: var(--font-size-18);
					font-weight: var(--font-weight-regular);
					color: var(--palette-live-grey-0);
					margin: 0 0 0 0;
				}
			}

			.tournament-participants__palyers-strength {
				width: 40px;
				height: 66.6px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;
				box-sizing: border-box;
				padding: 11.5px 0;
				font-size: var(--font-size-18);
				border-left: 1px solid var(--palette-live-grey-5);
				border-right: 1px solid var(--palette-live-grey-5);
				margin-left: 10px;
			}

			.tournament_participants__dnd--doubles {
				width: 40px;
				display: flex;
				justify-content: center;

				svg {
					width: 20px;
				}
			}
		}
	}
}