.group-player__single {
	display: flex;
	justify-content: space-between;
	padding: 0 20px 0 20px;
	align-items: center;
	cursor: pointer;

	&-wrapper {
		display: flex;
		flex: 1 1;
		flex-wrap: wrap;
		align-items: center;

		.group-player__single-index {
			width: 30px;
			margin: 0;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);
		}

		.group-player__single-name {
			margin: 0 0 0 0;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);
		}

		.group-player__single-rating {
			margin: 0 0 0 4px;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);
		}
	}
}

.group-player__double {
	display: flex;
	justify-content: space-between;
	// padding: 0 16px;
	padding: 0px 20px 0px 8px;
	align-items: center;
	cursor: pointer;

	&-wrapper {
		display: flex;
		flex: 1 1;
		flex-wrap: wrap;
		align-items: center;

		.group-player__double-index {
			width: 30px;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);
		}

		.group-player__double-notplaying {
			margin: 0 0 10px 0;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);

			&:last-child {
				margin: 0 0 0 0;
			}
		}

		.group-player__double-name {
			margin: 0 0 2px 0;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);

			&:last-child {
				margin: 0 0 0 0;
			}
		}

		.group-player__double-rating {
			margin: 0 0 0 20px;
			font-size: var(--font-size-18);
			color: var(--palette-live-grey-0);
			font-weight: var(--font-weight-regular);
		}
	}
}

.group-player__buttons {
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: grab;
	height: 100%;

	.drag-icon {
		width: 24px;
		height: 100%;
		fill: var(--palette-live-grey-6);
	}
}

.name-line {
	width: 55px;
	height: 1px;
	background-color: var(--palette-live-grey-0);
	display: block;
}